<template>
  <div class="pnl_wraper">
     <log-header></log-header>

       <Tabs active-key="" >
        <Tab-pane label="" key="">
          <Button icon="ios-add" @click="add" class="btn_add"></Button>
          <Button icon="ios-trash" :disabled="isDisabled" class="btn_delete" @click="removeSelect" type="error"></Button>
          <div style="clear:both"></div>
          <Table height="600" :content="self" :columns="columns" @on-selection-change="changeSelect"  :data="warnData">
            <template slot-scope="{ row, index }" slot="action">
              <Button size="small" @click="edit(index)"></Button>&nbsp;&nbsp;
              <Button type="error" size="small" @click="del(index)"></Button>
            </template>
          </Table>
          <div class="modal" style="display:block" v-if="showDialog" role="dialog">
            <div class="modal-dialog">
              <div class="modal-content">
                <div class="modal-header">
                  <h5 class="modal-title">{{isEdit?'':''}}</h5>
                  <button type="button" class="close" data-dismiss="modal" @click="showDialog=false" aria-label="">
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div class="modal-body">
                  <Form :model="dataInfo" ref="dataForm" :label-width="80">
                    <FormItem label="" required>
                        <Input v-model="dataInfo.appName" placeholder=""  />
                    </FormItem>
                    <FormItem label="">
                        <Input v-model="dataInfo.className" placeholder="" />
                    </FormItem>
                    <FormItem label="" required>
                        <Input v-model="dataInfo.receiver" placeholder=""  />
                    </FormItem>
                    <FormItem label="" required>
                        <Input v-model="dataInfo.webhookUrl" placeholder=""  />
                    </FormItem>
                    <FormItem label="" required>
                        <Input v-model="dataInfo.errorCount" type="number" placeholder=""  />
                    </FormItem>
                    <FormItem label="" required>
                        <Input v-model="dataInfo.time" type="number" placeholder="(s)"  />
                    </FormItem>
                    <FormItem label="">
                         <i-switch v-model="dataInfo.status" size="large">
                            <span slot="open"></span>
                            <span slot="close"></span>
                        </i-switch>
                    </FormItem>
                  </Form>
                </div>
                <div class="modal-footer">
                  <button type="button" class="btn btn-secondary" data-dismiss="modal" @click="showDialog=false"></button>
                  <button type="button" class="btn btn-primary" @click="save">{{isEdit?'':''}}</button>
                </div>
              </div>
            </div>
          </div>
        </Tab-pane>
         <Tab-pane label="" key="">
           <div v-if="logs.length>0">
              <Button icon="ios-trash" class="btn_clear"  @click="clearWarn"></Button>
              <ul class="logList">
                <li v-for="(log,index) in logs" :key="index">
                  <div class="time">{{formatTime(log.dataTime)}}</div>
                  <div class="cnt"><span class="key">: </span>{{log.appName}}</div>
                  <div class="cnt"><span class="key">: </span>{{log.className}}</div>
                  <div class="cnt"><span class="key">: </span>{{log.time}}</div>
                  <div class="cnt"><span class="key">: </span>{{log.errorCount}}</div>
                  <div class="btn_showDetail">
                    <a href="javascript:void(0)" @click="doSearch(log)">>></a>
                  </div>
                </li>
              </ul>
              <Button @click="getMore" v-if="showMore" class="btn_more"></Button>
           </div>
            <div v-else style="text-align:center;padding-top:50px"></div>
        </Tab-pane>
       </Tabs>
      <confirm-delete v-model="showConfirm" @on-confirm="confirmPassword"></confirm-delete>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";
import axios from '@/services/http'
import _ from 'lodash'
import moment from 'moment'
import dateOption from './dateOption';
import 'view-design/dist/styles/iview.css';
import logHeader from '@/components/logHeader.vue'
import confirmDelete from '@/components/confirmDelete.vue'
import "@/assets/less/base.less";

export default {
  name: "Warn",
  data(){
   return {
    selection:[],
    showConfirm:false,
    dataInfo: {
        appName: '',
        className: '',
        receiver:'',
        webhookUrl:'',
        time: 60,
        status:false,
    },
    pageSize:50,
    from:0,
    logs:[],
    warnData:[],
    showDialog:false,
    columns:[
      {
        type: 'selection',
        width: 60,
        align: 'center'
      },
      {
        title: 'ID',
        width:150,
        key:'id'
      },
      {
        title: '',
        key:'appName'
      },
      {
        title: '',
        key:'className'
      },
      {
        title: '',
        width:150,
        key:'receiver'
      },
      {
        title: '',
        width:150,
        key:'errorCount'
      },
      {
        title: 'webHook',
        key:'webhookUrl'
      },
      {
        title: '',
        width:100,
        key:'time'
      },
      {
          title: '',
          slot: 'action',
          width: 150,
          align: 'center'
      }
    ],
    self:this,
    showMore:true,
    isEdit:false
   }
  },
  computed:{
    isDisabled(){
      return this.selection.length==0;
    }
  },
  components: {
    logHeader,
    confirmDelete
  },
  methods:{
    removeSelect(){
       if(this.selection.length>0 && confirm('')){
          var ps = [];
          for(var info of this.selection){
            ps.push(this.delIndex(info))
          }
          Promise.all(ps).then(()=>{
             this.$Message.success('');
              this.getData();
          })
       }
    },
    changeSelect(selection){
      this.selection = selection;
    },
    add(){
      this.initDataInfo();
      this.isEdit = false;
      this.showDialog = true;
    },
    delIndex(_info){
      return new Promise((res,rej)=>{
        axios.post(process.env.VUE_APP_API+'/deleteWarningRule?id='+_info.id).then(data=>{
          if(data.data.success){
           res();
          }
          else
          {
            rej();
          }
        })
      })
    },
    del(index){
      let _info = this.warnData[index];
      if(confirm('ID '+_info.id+' ')){
        this.delIndex(_info).then(()=>{
            this.$Message.success('');
            this.getData();
        })
      }
    },
    edit(index){
      this.dataInfo = this.warnData[index];
      this.isEdit = true;
      this.showDialog = true;
    },
    save(){
      if(this.dataInfo.appName==''){
        this.$Message.error('');
        return false;
      }
      else if(this.dataInfo.receiver==''){
        this.$Message.error('');
        return false;
      }
      else if(this.dataInfo.webhookUrl==''){
        this.$Message.error('');
        return false;
      }
      else if(this.dataInfo.errorCount==''){
        this.$Message.error('');
        return false;
      }
      else if(this.dataInfo.time==''){
        this.$Message.error('');
        return false;
      }
      this.setData(this.dataInfo);

    },
    initDataInfo() {
      this.dataInfo = {
        appName: '',
        className: '',
        receiver:'',
        webhookUrl:'',
        errorCount: 10,
        status:true,
        time: 60
      };
    },
    setData(info){
       let _info = _.clone(info)
       let id = _info.id || Date.now();
      
       _info.status = _info.status ? 1 : 0;
       axios.post(process.env.VUE_APP_API+'/saveWarningRuleList?id='+id,_info).then(data=>{
         if(data.data.success){
           this.$Message.success('');
           this.showDialog = false;
           this.getData();
         }
       })
    },
    getData(){
      this.$Loading.start();
      axios.get(process.env.VUE_APP_API+'/getWarningRuleList').then(data=>{
         this.$Loading.finish();
         this.warnData = _.get(data,'data',[]).map(item=>{
           return {
             ...item,
             status:item.status ==1
           }
         });
       })
    },
    formatTime(time){
      if(time){
        return moment(time).format('YYYY-MM-DD HH:mm:ss')
      }
      return ''
    },
    getLog(){
      //  
      axios.post(process.env.VUE_APP_API+'/query?index=plumelog_monitor_message_key'+'&from='+this.from+'&size='+this.pageSize,{
        "query": {
            "match_all": {}
        },
        "sort" : [
           { "dataTime" : "desc" },
        ]
      }).then(data=>{
        let logs=_.get(data,"data.hits.hits",[]).map(item=>{
          return {
            ...item._source
          }
        })
        if(logs.length==this.pageSize){
          this.showMore = true;
        }
        else
        {
          this.showMore = false;
        }
        this.logs.push.apply(this.logs,logs);
      })
    },
    getMore(){
      this.from += this.pageSize;
      this.getLog();
    },
    doSearch(info){
      this.$emit('init',{})
      this.$router.push({
        name:"Home",
        query:{
          className:info.className,
          appName:info.appName,
          time:info.dataTime-1000*60+','+info.dataTime,
          logLevel:'ERROR'
        }
      })
    },
    clearWarn(){
      this.showConfirm = true;
    },
    confirmPassword(pwd){
      axios.get(process.env.VUE_APP_API+'/deleteIndex?index=plumelog_monitor_message_key&adminPassWord='+pwd).then(result=>{
        console.log(result)
        if(result.data.acknowledged){
           alert('');
           this.logs=[];
        }
        else
        {
          alert(results[0].data.message);
        }
      })
    }
  },
  mounted(){
    this.getData();
    this.getLog();
  }
}
</script>
