.ivu-tabs-tabpane
{
  text-align: left;
}

.logList
{
  li{
    text-align: left;
    padding-left:20px;
    margin-bottom:30px;
    .time{
      padding-bottom:8px;
      border-bottom:1px dotted #ccc;
      margin-bottom:10px;
    }
    .btn_showDetail {
      margin-top:10px;
      padding-left:20px;
      font-size:12px;

    }
    .cnt{
      .key{
        display: inline-block;
        width:80px;
        padding-right:10px;
        text-align: right;
        font-size:12px;
        font-weight: 700;
      }
    }
  }
}
  .modal
  {
    z-index: 1000;
  }
  .modal-backdrop
  {
    z-index:900;
  }
  .ivu-tabs-nav-scroll
  {
    margin-top:20px;
  }
  .ivu-form-item
  {
    margin-bottom:5px;
  }
  .btn_add
  {
    float: right;
    margin:10px 20px 10px 0;
  }
  .btn_delete
  {
    float: right;
    margin: 10px 10px 10px 0;
  }
  .btn_clear
  {
    margin:0 0 20px 10px;
  }
</style>