import axios from 'axios'

/** axios  **/
axios.defaults.timeout = 300000
/** axios Request  **/
axios.interceptors.request.use(
  config => {
    return config
  },
  error => {
    // 
    return Promise.reject(error)
  }
)
/** axios Response  **/
axios.interceptors.response.use(
  response => {
    return response
  },
  error => {
    return Promise.reject(error)
  }
)
export default axios
